html,body{margin:0;padding:0;font-family:"Roboto", sans-serif;background:white;color:#444}.App{padding:20px}nav.navbar{display:flex;padding:0px 20px;background:#686de0}button.navbar-button{padding:5px 20px;width:100px;margin:15px 10px;border-radius:3px}#home-btn{color:white;text-decoration:none}.product-details{width:80%;height:auto;position:fixed;top:0;left:10%;display:flex;background-color:#eef2f7}.cart-slide{width:40%;height:100vh;position:fixed;top:0;right:0;border-radius:5px;padding:10px;display:flex;background-color:white;z-index:1}.quantity{padding:10px;width:150px}.cart-details .quantity{color:#c8c8c8}.cart-details .price{color:#444;font-weight:500;font-size:1.3rem}.totals div{display:flex;justify-content:space-around;align-items:center}.totals h5{color:#c8c8c8;width:30%;text-align:left}.totals .final-total h5,.totals .final-total p{color:#444;font-size:1.2rem;font-weight:500}.product-actions{display:flex;justify-content:space-around;align-items:center}.product-actions p{color:#c8c8c8;width:30%;margin:5px}.product-actions h5{color:#444;font-size:1.1rem;font-weight:500;margin:5px}.text-placeholder{border-radius:5px;width:200px;height:20px;flex:1 1}.icon-placeholder{width:40px;height:40px;border-radius:50%;flex:40px 0;margin-right:20px}.refresh{padding:10px;position:absolute;background:rgba(0,0,0,0.4);border-radius:10px;width:20px;height:20px;top:10px;right:10px;display:flex;justify-content:center;align-items:center;cursor:pointer}ul,li{margin:0;padding:0}.nav-btn{outline:none;border:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;cursor:pointer;position:absolute;top:18px;left:15px;width:50px;height:50px;border-radius:50%;background:transparent;z-index:2}ul{padding:25px;position:absolute;top:100px;width:230px;z-index:2}li{list-style:none;margin-bottom:20px;display:flex;align-items:center;cursor:pointer}.background{position:absolute;top:0;left:0;bottom:0;width:300px;background:#fff;z-index:2}div.background-close{width:0}nav{position:absolute;top:0;left:0;bottom:0;width:0px}.quantity{display:flex;justify-content:space-evenly;margin:10px 0;background-color:#fff;border-radius:25px}.slide{position:fixed;top:0px;height:0px;width:0px;background-color:red;transition:all 1s}.slide-toggle{height:100vh;width:100vw}.checkmark{width:40px;height:40px;border-radius:50%;margin:0 auto;display:block;stroke-width:2;stroke:#fff;stroke-miterlimit:10;box-shadow:inset 0px 0px 0px #7ac142;-webkit-animation:fill 0.4s ease-in-out 0.4s forwards, scale 0.3s ease-in-out 0.9s both;animation:fill 0.4s ease-in-out 0.4s forwards, scale 0.3s ease-in-out 0.9s both}.checkmark__circle{stroke-dasharray:166;stroke-dashoffset:166;stroke-width:2;stroke-miterlimit:10;stroke:#7ac142;fill:none;-webkit-animation:stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;animation:stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards}.checkmark__check{transform-origin:50% 50%;stroke-dasharray:48;stroke-dashoffset:48;-webkit-animation:stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;animation:stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards}@-webkit-keyframes stroke{100%{stroke-dashoffset:0}}@keyframes stroke{100%{stroke-dashoffset:0}}@-webkit-keyframes scale{0%,100%{transform:none}50%{transform:scale3d(1.1, 1.1, 1)}}@keyframes scale{0%,100%{transform:none}50%{transform:scale3d(1.1, 1.1, 1)}}@-webkit-keyframes fill{100%{box-shadow:inset 0px 0px 0px 30px #7ac142}}@keyframes fill{100%{box-shadow:inset 0px 0px 0px 30px #7ac142}}.nav-close{display:none}.page-container{margin-top:80px;background-color:#eef2f7;border-radius:0 0 0 50%}.login{position:fixed;top:200px;-webkit-animation:log 1s;animation:log 1s;background-color:white;padding:50px}@-webkit-keyframes log{0%{left:100vw}100%{left:0}}@keyframes log{0%{left:100vw}100%{left:0}}.detail-div{width:100%;background:#eef2f7;padding:15px;border:1px solid #444;border-radius:5px;height:90vh}.header-info{position:relative;top:100px;left:5%;display:none}@media screen and (min-width: 1023px){.heade